<template>
	<div class="entDtlMain">
		<div class="mainTitle">
			<span>企业名称：</span>
			<strong>{{ this.entInfo.entName }}</strong>
			<span></span>
			<span>统一社会信用代码/注册号：</span>
			<strong>{{ this.entInfo.entCode }}</strong>
		</div>

		<table class="entDtlTbIII">
			<tbody>
				<tr>
					<th><span style="color: red;position: relative;top: 3px;right: 4px;">*</span>中共党员（包括预备党员）人数</th>
					<td><input type="number" min="0" placeholder="请输入中共党员（包括预备党员）人数" v-model="partyBuilding.pMNum" /></td>
					<td>不公示</td>
				</tr>
				<tr>
					<th><span style="color: red;position: relative;top: 3px;right: 4px;">*</span>党组织拟建制情况</th>
					<td>
						<label>
							<input type="radio" name="pOS" value="党委" v-model="partyBuilding.pOS" />
							<span>党委</span>
						</label>
						<label>
							<input type="radio" name="pOS" value="党总支" v-model="partyBuilding.pOS" />
							<span>党总支</span>
						</label>
						<label>
							<input type="radio" name="pOS" value="党支部" v-model="partyBuilding.pOS" />
							<span>党支部</span>
						</label>
						<label>
							<input type="radio" name="pOS" value="未成立党组织" v-model="partyBuilding.pOS" />
							<span>未成立党组织</span>
						</label>
					</td>
					<td>不公示</td>
				</tr>
				<tr>
					<th><span style="color: red;position: relative;top: 3px;right: 4px;">*</span>负责人是否党员</th>
					<td>
						<label>
							<input type="radio" name="isPrincipalPM" value="是" v-model="partyBuilding.isPrincipalPM" />
							<span>是</span>
						</label>
						<label>
							<input type="radio" name="isPrincipalPM" value="否" v-model="partyBuilding.isPrincipalPM" />
							<span>否</span>
						</label>
					</td>
					<td>不公示</td>
				</tr>
				<tr>
					<th><span style="color: red;position: relative;top: 3px;right: 4px;">*</span>负责人是否党组织书记</th>
					<td>
						<label>
							<input type="radio" name="isPrincipalPOS" value="是" v-model="partyBuilding.isPrincipalPOS" />
							<span>是</span>
						</label>
						<label>
							<input type="radio" name="isPrincipalPOS" value="否" v-model="partyBuilding.isPrincipalPOS" />
							<span>否</span>
						</label>
					</td>
					<td>不公示</td>
				</tr>
			</tbody>
		</table>

		<div class="btnBox fRow">
			<div class="btn hover" @click="save()" v-show="!isSeeAnswer">保存</div>
			<div class="btn hover" @click="close()">关闭</div>
			<div class="btn hover" @click="backTo()" v-show="isSeeAnswer">返回</div>
		</div>
	</div>
</template>

<script>
import { mapState } from 'vuex';

export default {
	name: 'PartyBuildingInfo',
	data: function() {
		return {
			isSeeAnswer: false,
			partyBuilding: '' //党建
		};
	},
	computed: {
		...mapState({
			province: state => state.registerProvince, //省份
			entInfo: state => state.entInfo //企业信息
		})
	},
	methods: {
		// 获取 党建
		getPartyBuilding() {
			this.partyBuilding = this.$store.state.partyBuilding;
		},
		// 保存按钮
		save() {
			if (this.partyBuilding.pMNum == '') {
				this.$message.error('请完善信息');
			} else {
				this.$store.dispatch('partyBuilding', this.partyBuilding); //党建
				this.$store.dispatch('fPartyBuilding', true); //已填写信息 党建
				this.$message({
					message: '保存成功',
					type: 'success'
				});
			}
		},
		// 关闭按钮
		close() {
			// 跳转 企业信息填报页面
			this.$router.push({
				path: '/enterpriseInfoFill/' + this.province
			});
		},
		// 返回按钮
		backTo() {
			this.isSeeAnswer = false;

			this.partyBuilding = this.$store.state.partyBuilding;
		}
	},
	mounted() {
		// 获取 党建
		this.getPartyBuilding();
	}
};
</script>
